<template>
  <div class="game" @click="deselect" v-touch:swipe.left="swipedLeft">
    <div class="game_start" v-html="i18n.tap"></div>
    <GamePlayers />
    <div
      class="game_board_container"
      :class="{'game_board_container-isPlayer' : game.player.id === game.playerId}"
    >
      <Tutorial v-if="game.settings.mode === 0 && game.status > 0" />
      <div
        class="game_board"
        :class="[
          {'game_board-busy' : game.busy},
          'game_board-size-' + game.settings.size,
          'game_board-player-' + game.settings.size,
          ]
        "
        @click="boardClick"
      >
        <GameCell v-for="(cell, i) in game.board" :cell="cell" :i="i" :key="i" />
      </div>
      <div class="game_highlight_wrapper">
        <div
          class="game_highlight_container"
          :class="['game_highlight_container-size-' + game.settings.size]"
        >
          <div class="game_highlight"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import AttackService from '@/services/attack.service'
import GameService from '@/services/game.service'
import TutorialService from '@/services/tutorial.service'
import GamePlayers from './GamePlayers.vue'
import GameCell from './GameCell.vue'
import Tutorial from './Tutorial.vue'

export default {
  name: 'Game',
  components: {
    GamePlayers,
    GameCell,
    Tutorial
  },
  computed: mapState({
    game: state => state.game,
    settings: state => state.game,
    i18n: state => state.i18n
  }),
  data: () => ({
    gameService: new GameService(),
    attackService: new AttackService(),
    tutorialService: new TutorialService()
  }),
  methods: {
    select (i, owner) {
      if (i === this.game.selected) {
        this.deselect()
      } else if (owner === this.game.player.id) {
        this.gameService.changeSelected(i)
      } else {
        if (this.game.settings.mode !== 0) {
          this.attackService.attack(i)
        }
      }
      if (this.game.settings.mode === 0) {
        this.tutorialService.tutorialStep(i)
      }
    },
    selectable (i) {
      const cell = this.game.board[i]
      if (cell.owner === this.game.player.id) {
        if (cell.dice > 1) {
          const attackables = cell.adjacents.filter(cellId => {
            return this.game.board[cellId].owner !== cell.owner
          })
          return attackables.length > 0
        }
      } else if (this.game.selected !== null) {
        if (i === this.game.selected) {
          return true
        }
        if (this.game.board[this.game.selected].adjacents.includes(i)) {
          return true
        }
      }
      return false
    },
    boardClick (e) {
      e.stopPropagation()
      if (this.game.status < 2 && !this.game.player.id === this.game.playerId) {
        this.gameService.startGame()
      }
    },
    deselect () {
      if (this.game.board.length < 1) {
        this.gameService.newGame()
        this.changeShowSettings(false)
      } else if (!this.game.busy) {
        this.changeSelected(null)
      }
    },
    getColor (playerId) {
      return this.game.players.filter(player => {
        return player.id === playerId
      })[0].color
    },
    connectors (cell) {
      const connectors = []
      const flags = [false, false, false, false]
      const i = cell.id
      const owner = cell.owner
      // check top
      if (!cell.top) {
        const topId = i - this.game.settings.size
        if (this.game.board[topId].owner === owner) {
          connectors.push('game_cell-connectTop')
          flags[0] = true
        }
      }
      // check right
      if (!cell.right) {
        const rightId = i + 1
        if (this.game.board[rightId].owner === owner) {
          connectors.push('game_cell-connectRight')
          flags[1] = true
        }
      }
      // check bottom
      if (!cell.bottom) {
        const bottomId = i + this.game.settings.size
        if (this.game.board[bottomId].owner === owner) {
          connectors.push('game_cell-connectBottom')
          flags[2] = true
        }
      }
      // check left
      if (!cell.left) {
        const leftId = i - 1
        if (this.game.board[leftId].owner === owner) {
          connectors.push('game_cell-connectLeft')
          flags[3] = true
        }
      }
      // set rounded inner corners
      if (flags[0]) {
        if (flags[3]) {
          connectors.push('game_cell-cornerTopLeft')
        }
        if (flags[1]) {
          connectors.push('game_cell-cornerTopRight')
        }
      }
      if (flags[2]) {
        if (flags[1]) {
          connectors.push('game_cell-cornerBottomRight')
        }
        if (flags[3]) {
          connectors.push('game_cell-cornerBottomLeft')
        }
      }
      // set rounded outer corners
      if (!cell.top && !flags[0]) {
        if (!cell.right && !flags[1]) {
          connectors.push('game_cell-connectTopRight')
        }
        if (!cell.left && !flags[3]) {
          connectors.push('game_cell-connectTopLeft')
        }
      }
      if (!cell.bottom && !flags[2]) {
        if (!cell.right && !flags[1]) {
          connectors.push('game_cell-connectBottomRight')
        }
        if (!cell.left && !flags[3]) {
          connectors.push('game_cell-connectBottomLeft')
        }
      }
      return connectors
    },
    swipedLeft () {
    },
    ...mapActions('game', ['changeSelected']),
    ...mapActions('settings', ['changeShowSettings'])
  }
}
</script>

<style lang="scss">
@keyframes highlightCell {
  0% {
    z-index: 3;
  }
  100% {
    z-index: 3;
  }
}

.game {
  padding: 20px 0 60px;
  position: relative;
  &_start {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 64px;
    text-align: center;
    line-height: 72px;
    text-shadow: 1px 1px var(--primary-color-light);
  }
  &_board,
  &_highlight_container {
    margin: 0 auto;
    border-radius: 25px;
    max-width: calc(100vw - 30px);
    max-height: calc(100vw - 30px);
    &-size {
      &-4 {
        width: 404px;
      }
      &-5 {
        width: 504px;
      }
      &-6 {
        width: 604px;
      }
    }
  }
  &_highlight_wrapper {
    position: absolute;
    top: 0;
    width: 100%;
    pointer-events: none;
  }
  &_highlight_container {
    position: relative;
    overflow: hidden;
    &-size {
      &-4 {
        height: 404px;
      }
      &-5 {
        height: 504px;
      }
      &-6 {
        height: 604px;
      }
    }
  }
  &_highlight {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: linear-gradient(
      90deg,
      transparent,
      hsla(0, 0%, 100%, 0.3),
      transparent
    );
    transform: translateX(-100%);
  }
  &_board {
    display: grid;
    grid-auto-rows: 1fr;
    border: 4px solid var(--border-color-light);
    margin: 0 auto;
    position: relative;
    opacity: 1;
    column-gap: 4px;
    row-gap: 4px;
    background-color: var(--border-color-light);
    transition: opacity 0.2s cubic-bezier(0.4, 0, 0.6, 1);
    &_container {
      position: relative;
      &-isPlayer {
        .game_highlight {
          animation: highlightSwipe 1s;
        }
        .game_cell:not(.game_cell-isPlayer) {
          animation: highlightCell 1s;
        }
      }
    }
    &-size {
      &-4 {
        grid-template-columns: 1fr 1fr 1fr 1fr;
        .game_cell {
          &_dices {
            max-width: calc(15.5vw - 2px);
            max-height: calc(25vw - 19px);
            &_left {
              max-height: calc(25vw - 19px);
            }
            &_right {
              max-height: calc(25vw - 19px);
            }
          }
          &_dice {
            max-height: calc(6.25vw - 1px);
            max-width: calc(6.25vw - 1px);
          }
          &:nth-child(4) {
            border-top-right-radius: 21px;
            &:before {
              border-top-right-radius: 21px;
            }
          }
          &:nth-last-child(4) {
            border-bottom-left-radius: 21px;
            &:before {
              border-bottom-left-radius: 21px;
            }
          }
        }
      }
      &-5 {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
        .game_cell {
          &_dices {
            max-width: calc(10vw - 2px);
            max-height: calc(20vw - 16px);
            &_left {
              max-height: calc(20vw - 16px);
            }
            &_right {
              max-height: calc(20vw - 16px);
            }
          }
          &_dice {
            max-height: 5vw;
            max-width: 5vw;
          }
          &:nth-child(5) {
            border-top-right-radius: 21px;
            &:before {
              border-top-right-radius: 21px;
            }
          }
          &:nth-last-child(5) {
            border-bottom-left-radius: 21px;
            &:before {
              border-bottom-right-radius: 21px;
            }
          }
        }
      }
      &-6 {
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
        .game_cell {
          &_dices {
            max-width: calc(8.333333333333333vw - 2px);
            max-height: calc(16.66666vw - 16px);
            &_left {
              max-height: calc(16.66666vw - 16px);
            }
            &_right {
              max-height: calc(16.66666vw - 16px);
            }
          }
          &_dice {
            max-height: 4.166666666666666vw;
            max-width: 4.166666666666666vw;
          }
          &:nth-child(6) {
            border-top-right-radius: 21px;
            &:before {
              border-top-right-radius: 21px;
            }
          }
          &:nth-last-child(6) {
            border-bottom-left-radius: 21px;
            &:before {
              border-bottom-right-radius: 21px;
            }
          }
        }
      }
    }
    &:empty {
      opacity: 0;
      pointer-events: none;
    }
    &-busy {
      .game_cell {
        pointer-events: none;
      }
    }
  }
}

@media (max-width: 767px) {
  .game {
    &_board,
    &_highlight_container {
      width: 404px;
    }
    &_highlight_container {
      height: 404px;
    }
  }
}
@media (max-width: 410px) {
  .game {
    &_board,
    &_highlight_container {
      width: 335px;
    }
    &_highlight_container {
      height: 335px;
    }
  }
}
@media (max-width: 374px) {
  .game {
    &_board,
    &_highlight_container {
      width: 330px;
    }
    &_highlight_container {
      height: 330px;
    }
  }
}
@media (max-width: 359px) {
  .game {
    &_board,
    &_highlight_container {
      width: 290px;
    }
    &_highlight_container {
      height: 290px;
    }
  }
}
</style>
