<template>
  <div class="gamePlayers_container" :class="{'gamePlayers_container-active' : game.status > 0}">
    <div class="gamePlayers" ref="players">
      <div
        v-for="(player) in game.players"
        class="gamePlayers_player"
        :key="player.id"
        :ref="'player' + player.id"
      >
        <div
          class="gamePlayers_player_color"
          :class="[
            {'gamePlayers_player_color-active' : player.id === playingId},
            {'gamePlayers_player_color-isPlayer' : player.id === game.playerId},
            'gamePlayers_player_color-' + player.color]"
        >{{player.size}}</div>
        <div v-if="player.reserve" class="gamePlayers_player_reserve">
          <span class="gamePlayers_player_reserve_icon icon icon-treasure"></span>
          {{player.reserve}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'GamePlayers',
  computed: mapState({
    game: state => state.game,
    playingId: state => state.game.player.id
  }),
  data: () => ({
    newGameDialog: false
  }),
  methods: {},
  watch: {
    playerId (val) {
      const offsetWidth = this.$refs.players.offsetWidth - 60
      const i = this.game.players.findIndex(i => i.id === val)
      const scrollStart = Math.floor(offsetWidth / 120)
      const scroll = i > scrollStart ? (i - scrollStart) * 60 - 30 : -30
      this.$refs.players.scrollTo({
        top: 0,
        left: scroll,
        behavior: 'smooth'
      })
    }
  }
}
</script>

<style scoped lang="scss">
.gamePlayers {
  height: 70px;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  scroll-snap-type: x mandatory;
  box-sizing: border-box;
  white-space: nowrap;
  text-align: center;
  width: 100%;
  padding: 0 10px;
  &_container {
    width: 100vw;
    position: relative;
    opacity: 0;
    transition: opacity 0.2s cubic-bezier(0.4, 0, 0.6, 1);
    -webkit-mask-image: linear-gradient(90deg, rgba(0,0,0,0) 10px, rgba(0,0,0,1) 30px, rgba(0,0,0,1) calc(100% - 30px), rgba(0,0,0,0) calc(100% - 10px));
    &-active {
      opacity: 1;
    }
  }
  &_player {
    display: inline-flex;
    align-items: center;
    margin: 0 10px;
    position: relative;
    scroll-snap-align: center;
    flex-direction: column;
    justify-content: center;
    width: 40px;
    &:first-child{
      margin-left: 30px
    }
    &:last-child{
      margin-right: 30px
    }
    &_color {
      width: 40px;
      height: 40px;
      border-radius: 4px;
      position: relative;
      font-size: 30px;
      line-height: 36px;
      text-align: center;
      box-sizing: border-box;
      color: var(--primary-color);
      transition: all 0.2s cubic-bezier(0.4, 0, 0.6, 1);
      position: relative;
      background-color: #fff;
      border: 3px solid transparent;
      &-active {
        color: #fff;
      }
      &-isPlayer {
        border-radius: 20px;
      }
      span {
        position: relative;
        z-index: 1;
      }
    }
    &_reserve {
      margin-top: 6px;
      display: flex;
      align-items: center;
      line-height: 18px;
      color: #fff;
      &_icon {
        margin-right: 4px;
      }
    }
  }
  @each $i, $color in $colors {
    &_player {
      &_color {
        &-#{$i} {
          border-color: var(--player-color-#{$i});
          &.gamePlayers_player_color-active {
            background-color: var(--player-color-#{$i});
          }
        }
      }
    }
  }
}

@media (min-width: 767px) {
  .gamePlayers {
    overflow: auto;
  }
}
</style>
