<template>
  <div class="multiplayer">
    <v-card>
      <v-card-title>{{i18n.multiplayer}}: {{isHost ? i18n.host : i18n.guest}}</v-card-title>
      <v-card-text>
        <div v-if="isHost">
          <qrcode-vue
            :value="'https://yannde.github.io/dicegame/?id=' + gameId"
            class="multiplayer_qr"
          ></qrcode-vue>
          <div class="multiplayer_link">{{gameId}}</div>
        </div>
        <div class="multiplayer_players">
          <div
            class="multiplayer_loader"
            :class="{'multiplayer_loader-loaded' : players && players.length > 1}"
          >
            <div
              v-for="player in 8"
              class="multiplayer_player multiplayer_player-loading"
              :key="player"
            ></div>
          </div>
          <div
            v-for="player in players"
            class="multiplayer_player icon"
            :class="[
              {'icon-player' : player.isHuman},
              {'icon-ai' : !player.isHuman},
              {'multiplayer_player-isPlayer' : player.id === game.playerId},
              'multiplayer_player-color-' + player.color
            ]"
            :key="player.id"
          />
        </div>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn text>{{i18n.cancel}}</v-btn>
        <v-btn
          v-if="isHost"
          depressed
          color="primary"
          large
          :disabled="!players || !players[1] || !players[1].isHuman"
          @click="startMultiplayer"
        >{{i18n.start}}</v-btn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import Fire from '@/plugins/firebase.js'
import { mapActions, mapState } from 'vuex'
import GameService from '@/services/game.service'
import MultiplayerService from '@/services/multiplayer.service'
import QrcodeVue from 'qrcode.vue'

export default {
  name: 'Multiplayer',
  components: {
    QrcodeVue
  },
  computed: mapState({
    game: state => state.game,
    gameId: state => state.game.gameId,
    i18n: state => state.i18n
  }),
  data: () => ({
    gameService: new GameService(),
    multiplayerService: new MultiplayerService(),
    playersRef: null,
    gameRef: null,
    isHost: null,
    players: []
  }),
  created () {
    this.isHost = !this.$route.params.id
  },
  mounted () {
    this.multiplayerService.initMultiplayer()
    const again = !!this.gameId
    this.changePlayerId(0)
    if (this.isHost) {
      this.gameService.setPlayers()
      this.multiplayerService.setupMultiplayer()
      if (again) {
        this.initConnections()
      }
    } else {
      if (again) {
        this.initConnections()
      } else {
        this.changeGameId(this.$route.params.id)
      }
    }
  },
  methods: {
    initConnections () {
      this.gameRef = Fire.database().ref(this.gameId)
      this.playersRef = Fire.database().ref(this.gameId + '/players')
      const self = this
      this.playersRef.on(
        'value',
        function (snapshot) {
          self.players = snapshot.val()
          if (!self.isHost && self.game.playerId === 0) {
            self.multiplayerService.joinMultiplayer(self.players)
          }
        },
        function (error) {
          console.log('Error: ' + error.code)
        }
      )
      if (!this.isHost) {
        this.gameRef.on(
          'value',
          function (snapshot) {
            if (
              snapshot.val() &&
              snapshot.val().board &&
              snapshot.val().players[0].size > 0
            ) {
              self.changePlayers(self.players)
              self.changeBoard(snapshot.val().board)
              self.multiplayerService.joinStartMultiplayer()
              self.playersRef.off()
              self.gameRef.off()
            }
          },
          function (error) {
            console.log('Error: ' + error.code)
          }
        )
      }
    },
    startMultiplayer () {
      this.playersRef.off()
      this.changePlayers(this.players)
      this.gameService.setUpGame()
      this.multiplayerService.startMultiplayer()
    },
    ...mapActions('game', ['changePlayers']),
    ...mapActions('game', ['changeBoard']),
    ...mapActions('game', ['changeGameId']),
    ...mapActions('game', ['changePlayerId'])
  },
  watch: {
    gameId (val) {
      this.initConnections()
    }
  }
}
</script>

<style scoped lang="scss">
.multiplayer {
  padding: 20px 20px 60px;
  position: relative;
  width: 100%;
  &_qr {
    display: flex;
    justify-content: center;
  }
  &_link {
    display: inline-block;
    margin: 20px auto 10px;
    text-align: center;
    color: var(--primary-color);
  }
  &_players {
    display: flex;
    position: relative;
    min-height: 60px;
  }
  &_loader {
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    opacity: 1;
    transition: opacity 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    &-loaded {
      opacity: 0;
    }
  }
  &_player {
    width: 40px;
    height: 40px;
    margin: 10px;
    border-radius: 4px;
    position: relative;
    font-size: 30px;
    line-height: 36px;
    text-align: center;
    box-sizing: border-box;
    color: #fff;
    position: relative;
    flex-shrink: 0;
    transition: all 0.1s cubic-bezier(0.4, 0, 0.6, 1);
    &-isPlayer {
      border-radius: 20px;
    }
    &-color {
      @each $i, $color in $colors {
        &-#{$i} {
          background-color: var(--player-color-#{$i});
        }
      }
    }
    &-loading {
      background: rgba(0, 0, 0, 0.12);
      &:after {
        content: "";
        height: 100%;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        transform: translateX(-100%);
        z-index: 1;
        background: linear-gradient(
          90deg,
          transparent,
          hsla(0, 0%, 100%, 0.3),
          transparent
        );
        animation: loading 1.5s infinite;
      }
    }
  }
}
</style>
