<template>
  <div class="gameFooterControls">
    <template v-if="game.status < 2 && game.settings.mode < 3">
      <div v-if="game.player.id === game.playerId">{{i18n.makeMove}}</div>
      <div v-else class="gameFooterControls_message">
        <v-btn
          color="primary"
          class="gameFooterControls_message_btn"
          large
          @click="start"
        >{{i18n.start}}</v-btn>
      </div>
    </template>
    <template v-else-if="game.status === 2">
      <template v-if="game.settings.mode === 3 && game.player.id === game.playerId">
        <GameFooterTimer />
      </template>
      <v-btn
        v-else-if="game.player.id === game.playerId"
        color="primary"
        :disabled="game.busy || game.settings.mode === 0"
        @click="next"
        large
      >{{i18n.next}}</v-btn>
    </template>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import GameService from '@/services/game.service'
import MultiplayerService from '@/services/multiplayer.service'
import GameFooterTimer from './GameFooterTimer.vue'

export default {
  name: 'GameFooterControls',
  components: {
    GameFooterTimer
  },
  computed: mapState({
    game: state => state.game,
    settingsState: state => state.settings,
    i18n: state => state.i18n
  }),
  data: () => ({
    gameService: new GameService(),
    multiplayerService: new MultiplayerService(),
    settings: { settings: {} },
    select: null,
    showSettings: true
  }),
  methods: {
    start () {
      this.gameService.startGame()
    },
    next () {
      this.gameService.nextPlayer()
    }
  }
}
</script>

<style lang="scss">
.gameFooterControls {
  grid-column: 1 / span 3;
  opacity: 1;
  width: 100%;
  text-align: center;
  &_message {
    &_btn {
      &-last {
        margin-top: 20px;
      }
      .icon {
        margin-right: 6px;
      }
    }
  }
}
</style>
