import store from '@/store'
import Fire from '@/plugins/firebase.js'
import { timeouts } from '../globals/globals'

export default class AttackService {
  chain (i) {
    const selected = store.getters['game/getSelected']
    // const player = store.getters['game/getPlayer']
    const board = store.getters['game/getBoard']
    const chain = store.getters['game/getChain']
    chain.push(
      { selected, i }
    )
    console.log('chain', chain)
    chain.forEach((el, x) => {
      board[el.i].chain = x + 1
    })
    store.dispatch('game/changeChain', chain)
    store.dispatch('game/changeBoard', board)
  }

  attack (i) {
    return new Promise(resolve => {
      const attackerId = store.getters['game/getSelected']
      const board = store.getters['game/getBoard']
      const playerId = store.getters['game/getPlayerId']
      const settings = store.getters['game/getSettings']
      const attacker = board[attackerId]
      const isPlayer = attacker.owner === playerId
      const defender = board[i]
      const defendeIsPlayer = defender.owner === playerId
      const attackerDice = this.dice(attacker.dice, isPlayer)
      const defenderDice = this.dice(defender.dice, defendeIsPlayer)
      const defenderOwner = defender.owner
      attackerDice.cell = attackerId
      defenderDice.cell = i
      store.dispatch('game/changeResults', { attacker: attacker.owner, defender: defenderOwner, attackerDice, defenderDice })
      let direction = 'top'
      if (i === attackerId + 1) {
        direction = 'right'
      } else if (i === attackerId - 1) {
        direction = 'left'
      } else if (i > attackerId) {
        direction = 'bottom'
      }
      board[i].direction = direction
      board[i].defender = true
      store.dispatch('game/changeBoard', board)
      if (isPlayer) {
        store.dispatch('game/changeBusy', true)
      }
      if (settings.mode === 4) {
        const move = store.getters['game/getResults']
        move.from = playerId
        move.attackerCell = attackerId
        move.defenderCell = i
        move.action = 'attack'
        this.emitMove(move)
      }
      setTimeout(() => {
        defender.defender = false
        if (attackerDice.total > defenderDice.total) {
          defender.dice = attacker.dice - 1
          defender.owner = attacker.owner
          if (!this.checkLost(defenderOwner)) {
            if (defender.dice > 1 && isPlayer) {
              store.dispatch('game/changeSelected', i)
            } else {
              store.dispatch('game/changeSelected', null)
            }
            this.calculateAdd(defenderOwner)
          }
        } else {
          store.dispatch('game/changeSelected', null)
        }
        this.calculateAdd(attacker.owner)
        attacker.dice = 1
        store.dispatch('game/changeCell', { i, data: defender })
        store.dispatch('game/changeCell', { i: attackerId, data: attacker })
        if (isPlayer) {
          store.dispatch('game/changeBusy', false)
        }
        resolve({ status: 200 })
      }, timeouts.executeAttack)
    })
  }

  dice (x, isPlayer = null) {
    const settings = store.getters['game/getSettings']
    let total = 0
    const dice = []
    let weighedDice = 6
    if (settings.mode === 1) {
      weighedDice = isPlayer ? 6.1 : 5.9
    }
    for (let i = 0; i < x; i++) {
      const val = Math.min(Math.max((Math.floor(Math.random() * weighedDice) + 1), 1), 6)
      dice.push(val)
      total += val
    }
    return { total, dice }
  }

  checkLost (owner) {
    const board = store.getters['game/getBoard']
    const players = store.getters['game/getPlayers']
    const playerId = store.getters['game/getPlayerId']
    const settings = store.getters['game/getSettings']
    if (!board.filter(cell => cell.owner === owner).length) {
      const index = players.findIndex(x => x.id === owner)
      if (players[index].id === playerId) {
        store.dispatch('game/changeStatus', 4)
        store.dispatch('settings/changeShowSettings', true)
        store.dispatch('game/changeSelected', null)
        if (settings.mode === 4) {
          const move = {
            action: 'lost',
            from: playerId
          }
          this.emitMove(move)
        }
      }
      players.splice(index, 1)
      store.dispatch('game/changePlayers', players)
      if (players.length === 1) {
        store.dispatch('game/changeSelected', null)
        if (players[0].id === playerId) {
          store.dispatch('game/changeStatus', 5)
          if (settings.mode === 4) {
            const move = {
              action: 'win',
              from: playerId
            }
            this.emitMove(move)
          }
          store.dispatch('settings/changeShowSettings', true)
        }
        return true
      }
    }
    return false
  }

  calculateAdd (playerId) {
    const players = store.getters['game/getPlayers']
    const board = store.getters['game/getBoard']
    const cellIds = this.getPlayerCells(playerId)
    let toAdd = 1
    const total = cellIds.reduce(function (accumulator, cellId) {
      return accumulator + board[cellId].dice
    }, 0)
    const cells = cellIds.length
    for (let i = 0; i < cellIds.length; i++) {
      if (cellIds[i] > -1) {
        const group = []
        group.push(cellIds[i])
        delete cellIds[i]
        for (let x = 0; x < group.length; x++) {
          board[group[x]].adjacents.forEach(adj => {
            if (cellIds.includes(adj)) {
              group.push(adj)
              const index = cellIds.indexOf(adj)
              delete cellIds[index]
            }
          })
        }
        toAdd = Math.max(group.length, toAdd)
      }
    }
    const player = players[players.findIndex(x => x.id === playerId)]
    if (player) {
      player.size = toAdd
      player.total = total
      player.cells = cells
      store.dispatch('game/changePlayers', players)
      const storePlayer = store.getters['game/getPlayer']
      if (player.id === storePlayer.id) {
        store.dispatch('game/changePlayer', player)
      }
    }
    return toAdd
  }

  getPlayerCells (playerId) {
    const cellIds = []
    store.getters['game/getBoard'].forEach((cell, i) => {
      if (cell.owner === playerId) {
        cellIds.push(i)
      }
    })
    return cellIds
  }

  emitMove (move) {
    const gameId = store.getters['game/getGameId']
    const newMoveKey = Fire.database().ref(gameId).child('moves').push().key
    Fire.database().ref(gameId + '/moves/' + newMoveKey).set({
      ...move
    })
  }
}
